  /**
   * 查询当前登陆用户的审核情况的未读数
   */
  getAuditCountApi(that) {
    axios({
      method: 'get',
      url: '/audit/userAuditCount',
      params: {},
      headers: {
        'auth-token': sessionStorage.getItem('auth-token')
      }
    }).then(function(res) {
      const data = res.data
      if (data.code === 20006) {
        Message.error(data.msg);
        setTimeout(() => { window.location.href = '/' }, 1000)
      } else {
        if (data.code === 1) {
          that.$store.dispatch('saveAuditCount', data.data)
        }
      }
    }).catch(function(error) {
      if (error.response.status === 400) {
        Message.error(error.response.data.errors[0].defaultMessage);
      } else {
        Message.error('网络连接错误');
      }
    });
  },
  /**
   * @description: 格式化日期
   * @param: date {Date}
   * @param: fmt {String}
   * @return: {string}
   * @author: Breaker-93
   * @date: create on 2018-01-25 15:36:49
   */
  formatDate(date, fmt) {
    var o = {
      'M+': date.getMonth() + 1, // 月份
      'd+': date.getDate(), // 日
      'H+': date.getHours(), // 小时
      'm+': date.getMinutes(), // 分
      's+': date.getSeconds(), // 秒
      'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
      'S': date.getMilliseconds() // 毫秒
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
    for (var k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)));
    }
    return fmt;
  },

  /**
   * @description: 根据浏览器url中的参数key获取值
   * @param: name url参数key{String}
   * @return: {string}
   * @author: Breaker-93
   * @date: create on 2018-01-25 15:39:28
   */
  getQueryString(name) {
    var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
    // 专获取先编码后加密的url的参数，例如： let goUrl = 'experiment.html?'+window.btoa(encodeURIComponent("paramBody="+JSON.stringify(paramBody)));
    var r = window.decodeURIComponent(window.atob(window.location.search.substr(1))).match(reg);
    if (r != null) return decodeURI(r[2]);
    return null;
  },
  getUrlParam(url, name) {
    var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
    // 专获取先编码后加密的url的参数，例如： let goUrl = 'experiment.html?'+window.btoa(encodeURIComponent("paramBody="+JSON.stringify(paramBody)));
    if (url.split('?').length === 1) {
      return null
    }
    var r = url.split('?')[1].match(reg);
    if (r != null) {
      return decodeURI(r[2]);
    } else {
      return null;
    }
  },
  transWeekDay(dateStr) {
    const weekDay = ['sun', 'mon', 'tue', 'wed', 'thu', 'fri', 'sat'];
    const targetDate = new Date(Date.parse(dateStr.replace(/-/g, '/')));
    return weekDay[targetDate.getDay()]
  },
  calcMonths(date1, date2) {
    date1 = date1.split('-');
    date1 = parseInt(date1[0]) * 12 + parseInt(date1[1]);
    date2 = date2.split('-');
    date2 = parseInt(date2[0]) * 12 + parseInt(date2[1]);
    return Math.abs(date1 - date2) + 1;
  },
  calcAgeByBirth(strBirthday) {
    var returnAge;
    var strBirthdayArr = strBirthday.split('-');
    var birthYear = strBirthdayArr[0];
    var birthMonth = strBirthdayArr[1];
    var birthDay = strBirthdayArr[2];

    var d = new Date();
    var nowYear = d.getFullYear();
    var nowMonth = d.getMonth() + 1;
    var nowDay = d.getDate();
    console.log(nowYear)
    if (nowYear === birthYear) {
      returnAge = 0 // 同年 则为0岁
    } else {
      var ageDiff = nowYear - birthYear // 年之差
      if (ageDiff > 0) {
        if (nowMonth === birthMonth) {
          var dayDiff = nowDay - birthDay // 日之差
          if (dayDiff < 0) {
            returnAge = ageDiff - 1
          } else {
            returnAge = ageDiff
          }
        } else {
          var monthDiff = nowMonth - birthMonth // 月之差
          if (monthDiff < 0) {
            returnAge = ageDiff - 1
          } else {
            returnAge = ageDiff
          }
        }
      } else {
        returnAge = -1 // 返回-1 表示出生日期输入错误 晚于今天
      }
    }
    return returnAge // 返回周岁年龄
  }
