<template>
  <div class="loginContent">
    <Icon size="12" type="md-log-in" /><b>津农汇通项目工作数据监控系统</b>
    <Tabs value="name1">
      <TabPane label="账号登录" name="name1">
        <Form ref="form" :model="form" :rules="ruleInline" inline>
          <FormItem prop="username">
            <Input v-model="form.username" type="text" placeholder="请输入用户名">
            <Icon slot="prepend" size="16" type="ios-person"></Icon>
            </Input>
          </FormItem>
          <FormItem prop="password">
            <Input v-model="form.password" type="password" placeholder="请输入密码" @on-enter="handleSubmit('form')">
            <Icon slot="prepend" size="16" type="ios-lock"></Icon>
            </Input>
          </FormItem>
          <FormItem>
            <Button type="primary" @click="handleSubmit('form')">
              登录
            </Button>
          </FormItem>
        </Form>
      </TabPane>
      <TabPane label="UK登录" name="name2">
        <Form ref="UK" class="uk" :model="form" inline>
          <img src="../assets/USB.png">
          <span>请插入UK</span>
          <FormItem>
            <Button style="margin-top: 83px;" type="primary">
              登录
            </Button>
          </FormItem>
        </Form>
      </TabPane>
    </Tabs>
  </div>
</template>

<script>
export default {
  data() {
    const validatePassCheck = (rule, value, callback) => {
      const number = /[0-9]/;
      const capital = /[a-zA-Z]/;
      const character = /[#?!@$%^&*-]/;
      if (value === '') {
        callback(new Error('请输入密码'));
      } else if (!number.exec(value) || !capital.exec(value) || !character.exec(value)) {
        callback(new Error('密码格式不正确'));
      } else if (value.length < 6) {
        callback(new Error('密码长度不能小于6位'));
      } else {
        if (this.form.passwordConfirm === this.form.password) {
          this.$refs.form.validateField('passwordConfirm')
        }
        callback();
      }
    };
    return {
      form: {
        username: '',
        password: ''
      },
      ruleInline: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        password: [
          { required: true, validator: validatePassCheck, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    clearStorage() {
      sessionStorage.removeItem('auth-token');
      sessionStorage.removeItem('menuList');
    },
    handleSubmit(name) {
      const that = this;
      that.$refs[name].validate((valid) => {
        if (valid) {
          that.$Spin.show();
          that.clearStorage();
          // 登陆
          that.commonUtil.ajaxPostUtil('/user/login', that.form, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              const data = res.data
              that.$store.dispatch('saveMenuList', data.menuList)
              that.$store.dispatch('saveNavbarList', data.navbarList)
              that.$store.dispatch('saveAuditCount', parseInt(data.auditCount))
              sessionStorage.setItem('auth-token', data.token);
              //  转换为字符串，再存数据
              that.$store.dispatch('saveUserInfo', {
                id: data.userId,
                username: data.username,
                orgName: data.orgName,
                manageOrgName: data.manageOrgName,
                capitalName: data.capitalName,
                linkmanName: data.linkmanName,
                projectName: data.projectName
              })
              that.$router.push('/home')
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          })
        }
      })
    }
  }
}
</script>

<style>
  html {
    height: 100%;
  }
  body {
    height: 100%;
    background: url("../assets/bg.jpg") no-repeat center center;
    background-size: cover;
  }
.loginContent {
  padding: 0px 15px;
  background-color: white;
  position: absolute;
  top: 50%;
  margin-top: -141.5px;
  right: 180px;
  -webkit-box-shadow: rgb(134, 134, 134) 1px 1px 5px;
  box-shadow: rgb(134, 134, 134) 1px 1px 5px;
  width: 320px;
  height: 264px;
  border-radius: 2%;
}
.loginContent b {
  display: inline-block;
  vertical-align: middle;
  margin: 14px 0;
  margin-left: 5px;
    font-size: 14px;
  }
.loginContent .ivu-form-inline .ivu-form-item, .ivu-form-item-content button {
  width: 100%;
}
.loginContent .ivu-tabs-nav .ivu-tabs-tab {
  font-size: 12px !important;
}
.ivu-tabs-tab {
  font-size: 12px !important;
}
  .uk img {
    margin-top: 6px;
    width: 14px;
  }
.uk img + span {
  font-weight: bold;
  margin-left: 5px;
  vertical-align: text-bottom;
}
</style>



