<template>
  <div class="layout">
    <Layout>
      <Header>
        <Menu mode="horizontal" @on-select="selectMenu">
          <img style="cursor: pointer" class="logoImg" src="../assets/logo.jpg" @click="toHome">
          <span class="clearBoth md-menu">
            <Icon size="20" type="md-menu" :class="rotateIcon" @click="collapsedSider" />
          </span>
          <span class="clearBoth">
            <Icon size="20" type="ios-cube" class="projectSystem" />
            津农汇通工作数据监控系统
          </span>
          <div class="layout-nav">
            <span style="cursor:pointer;line-height: 62px;float: right; margin-left: 18px; margin-top: -1px; margin-right: -15px;" @click="showContact = true">
              <Icon size="20" type="md-more" />
            </span>
            <span style="cursor: pointer;line-height: 58px;float: right;margin-left: 11px;" @click="toMessage">
              <Icon size="20" type="ios-mail" />
            </span>
            <!-- 审核情况|表单模板 -->
            <MenuItem v-for="item in navbarList" :key="item.adminMenuId" :name="item.adminMenuUrl">
              <div v-if="item.adminMenuName==='审核情况'">
                <Badge :count="auditCount" class-name="badgeInfo">
                  <Icon :type="item.adminMenuIcon" size="20"></Icon>
                </Badge>
                {{ item.adminMenuName }}
              </div>
              <div v-else>
                <Icon :type="item.adminMenuIcon"></Icon>
                {{ item.adminMenuName }}
              </div>
            </MenuItem>
            <span style="line-height: 62px;float: right;">
              <Dropdown>
                <a href="javascript:void(0)">
                  <Icon size="20" type="md-people" />
                  <span style="font-size: 12px;margin-left: 3px;">{{ userInfo.username }}</span>
                </a>
                <DropdownMenu slot="list">
                  <DropdownItem @click.native="exitHome">退出</DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </span>
          </div>
        </Menu>
      </Header>
      <Layout id="layout">
        <Sider ref="sideBar" v-model="isCollapsed" :style="{position: 'fixed', height: '100vh', overflow: 'auto', background: '#fff'}" hide-trigger collapsible :collapsed-width="78">
          <Menu v-if="slideMenu ===''&&menuList.length>0" ref="side_menu" style="padding-bottom: 100px;" accordion theme="light" width="auto" :class="menuitemClasses" :active-name="activeName" @on-select="navigateTo">
            <Submenu v-for="item in menuList" :key="item.adminMenuId" :name="item.adminMenuId">
              <template slot="title">
                <Icon :type="item.adminMenuIcon" :style="{color:(item.adminMenuColor||'#000')}" />
                <span :style="{color:(item.adminMenuColor||'#000')}">{{ item.adminMenuName }}</span>
              </template>
              <!--二级菜单-->
              <div v-if="item.menuModelList.length>0">
                <MenuItem v-for="menu in item.menuModelList" :key="menu.adminMenuId" :name="JSON.stringify({menuPId:item.adminMenuId,menuId:menu.adminMenuId,adminMenuUrl:menu.adminMenuUrl})">
                  {{ menu.adminMenuName }}
                </MenuItem>
              </div>
            </Submenu>
          </Menu>
          <Menu v-else-if="slideMenu === '/checkInfo'" accordion :active-name="currentMenu" theme="light" width="auto" :class="menuitemClasses" @on-select="navigateTo">
            <Submenu name="1">
              <template slot="title">
                <!--审核情况目录-->
                <Icon type="ios-navigate"></Icon>
                <span>审核情况</span>
              </template>
              <!--审核情况目录-->
              <MenuItem :name="JSON.stringify({adminMenuUrl:'/checkInfo'})">
                审核情况
              </MenuItem>
            </Submenu>
          </Menu>
          <!--表单模板-->
          <Menu v-else-if="slideMenu === '/formModel'" accordion :active-name="currentMenu" theme="light" width="auto" :class="menuitemClasses" @on-select="navigateTo">
            <Submenu name="1">
              <template slot="title">
                <!--审核情况目录-->
                <Icon type="ios-navigate"></Icon>
                <span>表单模板</span>
              </template>
              <!--二级菜单-->
              <!--审核情况目录-->
              <MenuItem :name="JSON.stringify({adminMenuUrl:'/formModel'})">
                表单模板
              </MenuItem>
            </Submenu>
          </Menu>
          <Menu v-else-if="slideMenu === '/message'" accordion :active-name="currentMenu" theme="light" width="auto" :class="menuitemClasses" @on-select="navigateTo">
            <Submenu name="1">
              <template slot="title">
                <Icon type="ios-navigate"></Icon>
                <span>消息通知</span>
              </template>
              <MenuItem :name="JSON.stringify({adminMenuUrl:'/message'})">
                消息通知
              </MenuItem>
            </Submenu>
          </Menu>
        </Sider>

        <Layout :style="{padding: '0 24px 24px',marginLeft: isCollapsed?'78px':'200px'}">
          <!--用户信息-->
          <div v-if="showHome" class="userStyle">
            <Content class="mainContent content di" :style="{padding: '14px 24px', background: '#fff'}">
              <Divider orientation="left">
                用户信息
              </Divider>
              <div style=" display: flex;align-items: center;justify-content: space-between;">
                <div style="width: 33.3%;">
                  <Icon :size="iconSize" type="md-person" />
                  <div>
                    <b>{{ userInfo.username }}</b>
                    <p>用户名</p>
                  </div>
                  <div>
                    <Icon :size="iconSize" type="md-list-box" />
                    <div>
                      <b>{{ userInfo.projectName }}</b>
                      <p>项目名称</p>
                    </div>
                  </div>
                </div>
                <Divider type="vertical" />
                <div style="width: 33.3%;">
                  <Icon :size="iconSize" type="ios-home" />
                  <div>
                    <b>{{ userInfo.orgName }}</b>
                    <p>使用单位</p>
                  </div>
                  <div>
                    <Icon :size="iconSize" type="ios-ribbon" />
                    <div>
                      <b>{{ userInfo.manageOrgName }}</b>
                      <p>主管单位</p>
                    </div>
                  </div>
                </div>
                <Divider type="vertical" />
                <div style="width: 33.3%;">
                  <Icon :size="iconSize" type="ios-school" />
                  <div>
                    <b>{{ userInfo.linkmanName }}</b>
                    <p>使用人</p>
                  </div>
                  <div>
                    <Icon :size="iconSize" type="ios-paper" />
                    <div>
                      <b>{{ userInfo.capitalName }}</b>
                      <p>资金类别</p>
                    </div>
                  </div>
                </div>
              </div>
            </Content>
          </div>
          <router-view v-else></router-view>
        </Layout>
      </Layout>
      <!--联系我们-->
      <Drawer v-model="showContact" title="联系我们" :closable="false">
        <div>
          <p>
            <Icon type="ios-call" size="18" />
            <b>技术支持</b>
          </p>
          <p>如果您有任何关于系统的问题，请发邮件给我们。发送邮件至：<span style="color: red">022-83869786</span></p>
        </div>
        <div>
          <p><Icon type="ios-call" size="18" /><b>传真</b></p>
          <p>如果您有任何关于系统的问题，请发邮件给我们。发送邮件至：<span style="color: red">022-83869786</span></p>
        </div>
        <div>
          <p><Icon type="ios-mail" size="18" /><b>电子邮箱</b></p>
          <p>如果您有任何关于系统的问题，请发邮件给我们。发送邮件至：<span style="color: red">tjjnht@163.com</span></p>
        </div>
        <div>
          <p><Icon type="md-locate" size="18" /><b>企业地址</b></p>
          <p>天津市河西区福盛花园（南区）9号楼底商6号津农汇通津河办公区</p>
        </div>
      </Drawer>
      <!--/联系我们-->
    </Layout>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userInfo: this.$store.state.user.userInfo,
      navbarList: this.$store.state.user.navbarList,
      menuList: this.$store.state.user.menuList,
      iconSize: '16',
      activeName: '',
      // 是否显示首页统计
      showHome: true,
      currentMenu: '',
      showContact: false,
      // 判断当前哪个目录
      slideMenu: '',
      isCollapsed: false
    }
  },
  computed: {
    auditCount() {
      return this.$store.state.user.auditCount
    },
    rotateIcon() {
      return [
        'menu-icon',
        this.isCollapsed ? 'rotate-icon' : ''
      ];
    },
    menuitemClasses() {
      return [
        'menu-item',
        this.isCollapsed ? 'collapsed-menu' : ''
      ]
    }
  },
  methods: {
    // 侧边栏的显示和隐藏
    collapsedSider() {
      this.$refs.sideBar.toggleCollapse();
    },
    // 信息信息页面
    toMessage() {
      this.$router.push('/message');
      this.slideMenu = '/message';
    },
    // 点击图标
    toHome() {
      this.activeName = ''
      this.slideMenu = '';
      this.$router.push('/home');
      this.showHome = true;
    },
    // 退出
    exitHome() {
      sessionStorage.removeItem('auth-token');
      sessionStorage.removeItem('menuList');
      this.$router.replace('/');
    },
    navigateTo(item) {
      if (item) {
        const obj = JSON.parse(item)
        this.$router.push(obj.adminMenuUrl);
        sessionStorage.setItem('archiveLocation', item)
        this.showHome = false;
      }
    },
    // 切换顶部菜单
    selectMenu(name) {
      this.slideMenu = name;
      this.showHome = false;
      this.$nextTick(() => {
        this.currentMenu = name
        this.$router.push(name)
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .badgeInfo{
    top:12px;
    min-width: 12px;
    height: 12px;
    line-height: 12px;
    font-size: 10px;
    border-radius: 10px;
  }
  .collapsed-menu .ivu-menu-submenu .ivu-menu-item{
    padding:14px 24px!important;
  }
  /* 控制munu按钮居中 */
  .collapsed-menu .ivu-menu-submenu-title{
    padding:14px 0px!important;
    text-align: center
  }
  .collapsed-menu .ivu-menu-submenu-title>i{
    margin-right:0px
  }
  .collapsed-menu .ivu-menu-submenu-title-icon{
    right:8px;
    display:none;
  }
  .clearBoth{
    clear: both;
    position: absolute;
    font-weight: bolder;
    font-size: 14px;
  }
  .layout .projectSystem{
    margin-left:35px;
    margin-right:10px;
  }
  .layout .md-menu{
    margin-left: 0px;
    cursor:pointer;
    z-index:10;
  }
  .ivu-menu-vertical.ivu-menu-light:after{
    content: "";
    display: table;
  }
  /* .layout-con{
    height: 100%;
    width: 100%;
  } */
  .ivu-drawer-body > div {
    margin: 20px 0;
  }
  .ivu-drawer-body > div b {
    margin-left: 3px;
  }
  .logoImg {
    /* padding: 0 10px; */
    height: 60px;
  }
  .ivu-layout-header .ivu-menu-light.ivu-menu-horizontal .ivu-menu-item, .ivu-menu-light.ivu-menu-horizontal .ivu-menu-submenu {
    float: right;
    font-size: 12px;
  }
  .ivu-layout-header .ivu-dropdown-item {
    text-align: center;
  }
  /*<!--选择弹出框的样式-->*/
  .selStyle .ivu-radio-wrapper {
    font-weight: bold;
  }
  .selStyle .ivu-modal-body {
    line-height: 2rem;
  }
  .selStyle .ivu-radio-wrapper {
    margin-right: 21px;
  }
  .ivu-page-options-sizer {
    margin-right: 0;
  }
  .ivu-layout {
    height: 100%;
    margin-top: -2px;
  }
  .menu-icon{
      transition: all .3s;
  }
  .rotate-icon{
    transform: rotate(-90deg);
  }
  .ivu-layout-header {
    background-color: white !important;
    padding: 0 !important;
  }
  .breadcrumb i {
    margin-right: 8px;
  }
  .ivu-breadcrumb {
    font-size: 12px;
    margin: 20px 0;
  }
  .ivu-menu-submenu-title{
    font-weight: bold;
  }
  .ivu-menu-submenu-title i {
    margin-right: 4px;
  }
  .ivu-menu-vertical .ivu-menu-item, .ivu-menu-vertical .ivu-menu-submenu-title {
    font-size: 12px;
    color: #495060
  }
  .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
    font-weight: bold;
  }
  .di .ivu-divider, .di .ivu-divider-vertical {
    margin-right: 30px !important;
    margin-left: 0 !important;
  }
  .layout{
    height: 100%;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
  .layout-logo{
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
  }
  .layout-nav{
    float: right;
    font-size: 12px;
    /* width: 420px; */
    margin: 0 auto;
    margin-right: 34px;
  }
  .mainContent {
    margin-top: 24px;
    padding-bottom: 24px !important;
  }
  .mainContent img {
    vertical-align: top;
    width: 14px !important;
  }
  .mainContent .ivu-icon {
    vertical-align: top;
  }
  .mainContent .ivu-icon + div {
    display: inline-block;
    margin-left: 6%;
    margin-bottom: 24px;
  }
  .mainContent b {
    font-size: 12px;
  }
  .mainContent .title{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .mainContent .title span {
    font-size: 32px;
  }
  .mainContent .title p{
    color: #657180;
  }
  .mainContent .title .ivu-divider, .ivu-divider-vertical {
    height: 7em;
  }
  .ivu-layout-sider {
    height: 100%;
  }
  .ivu-divider-inner-text {
    font-weight: bold;
  }
  .mainContent .ivu-tabs-bar {
    border-bottom: 0;
  }
  .mainContent .ivu-col-span-8 {
    margin-bottom: 13px;
  }
  /*图标颜色*/
  .userStyle i {
    color: #2d8cf0;
  }

  .menu-item span{
      display: inline-block;
      overflow: hidden;
      width: auto;
      text-overflow: ellipsis;
      white-space: nowrap;
      vertical-align: bottom;
      transition: width .2s ease .2s;
  }
  .menu-item i{
      transform: translateX(0px);
      transition: font-size .2s ease, transform .2s ease;
      vertical-align: middle;
      font-size: 16px;
  }
  .collapsed-menu span{
      width: 0px;
      transition: width .2s ease;
  }
  .collapsed-menu span i{
      transform: translateX(5px);
      transition: font-size .2s ease .2s, transform .2s ease .2s;
      vertical-align: middle;
      font-size: 22px;
  }
  .ivu-layout-header .ivu-menu-light.ivu-menu-horizontal .ivu-menu-item, .ivu-menu-light.ivu-menu-horizontal .ivu-menu-submenu {
    font-size: 13px;
    vertical-align: middle !important;
  }
</style>



