}
.curBreadcrumb .ivu-breadcrumb-item-separator {
  display: none;
}
/*tab页表格的按钮*/
.innerTabStyle .ivu-table-cell button {
  padding: 0;
  color: #2d8cf0;
}
.screenContent {
  color: #515a6e;
  font-weight: bold;
  float: right;
}
.uploadStyle b i {
margin-right: 8px;
}
.uploadBtn i {
  margin-left: 2px;
}
.ivu-upload-select {
  cursor: pointer;
  color:blue
}
.ivu-upload-list .ivu-icon-md-document{
