const user = {
  state: {
    userInfo: sessionStorage.getItem('userInfo') ? JSON.parse(sessionStorage.getItem('userInfo')) : {},
    auditCount: sessionStorage.getItem('auditCount') ? parseInt(sessionStorage.getItem('auditCount')) : 0,
    menuList: sessionStorage.getItem('menuList') ? JSON.parse(sessionStorage.getItem('menuList')) : [],
    navbarList: sessionStorage.getItem('navbarList') ? JSON.parse(sessionStorage.getItem('navbarList')) : []
  },

  mutations: {
    // 保存登录信息
    SET_USER_INFO: (state, userInfo) => {
      sessionStorage.setItem('userInfo', JSON.stringify(userInfo));
      state.userInfo = userInfo
    },
    // 保存登录信息
    SET_AUDIT_COUNT: (state, auditCount) => {
      sessionStorage.setItem('auditCount', parseInt(auditCount));
      state.auditCount = auditCount
    },
    // 菜单列表
    SET_MENULIST: (state, menuList) => {
      sessionStorage.setItem('menuList', JSON.stringify(menuList));
      state.menuList = menuList
    },
    SET_NAVBARLIST: (state, navbarList) => {
      sessionStorage.setItem('navbarList', JSON.stringify(navbarList));
      state.navbarList = navbarList
    }
  },

  actions: {
    saveUserInfo({ commit }, userInfo) {
      commit('SET_USER_INFO', userInfo);
    },
    saveAuditCount({ commit }, auditCount) {
      commit('SET_AUDIT_COUNT', auditCount);
    },
    saveMenuList({ commit }, menuList) {
      commit('SET_MENULIST', menuList);
    },
    saveNavbarList({ commit }, navbarList) {
      commit('SET_NAVBARLIST', navbarList);
    }
  }
}

export default user



