/**
 * 关于微信小程序swaggerUI，重新新建axios文件
 */
const axios = require('axios')
const baseUrl = 'http://api.tjjnht.cn/api'
import { Message } from 'iview'
export default {
  ajaxPostUtil(url, data, thenFun, exeFun, that) {
    axios({
      method: 'post',
      url: baseUrl + url,
      data: data
    }).then(function(res) {
      thenFun.call(this, res.data);
    }).catch(function(error) {
      that.$Spin.hide();
      if (error.response.status === 400) {
        Message.error(error.response.data.message);
      } else {
        exeFun.call(this, error);
      }
    });
  },
  ajaxGetUtil(url, param, thenFun, exeFun, that) {
    axios({
      method: 'get',
      url: baseUrl + url,
      params: param
    }).then(function(res) {
      thenFun.call(this, res.data);
    }).catch(function(error) {
      that.$Spin.hide();
      if (error.response.status === 400) {
        Message.error(error.response.data.message);
      } else {
        exeFun.call(this, error);
      }
    });
  }
}



