<template>
  <div style="width:100%;">
    <h3 v-if="list.length===0" style="margin:15px 0px;text-align:center;">暂无数据</h3>
    <div v-else>
      <Button v-show="isPrint" type="primary" icon="md-print" style="margin:15px 0px;" @click="print">打印数据</Button>
      <table class="ivu-table" cellspacing="0" style="width:100%">
        <thead>
          <tr>
            <th>序号</th>
            <th v-for="(item,key) in columns" :key="key">{{ item.name }}</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item,key) in list" :key="key">
            <th>{{ key+1 }}</th>
            <td v-for="(content,j) in columns" :key="j">
              <img v-if="content.barCode" :id="'code'+item.id" style="width: 180px;height: 100px;">
              <span v-else>
                {{ item[content.key] }}
              </span>
            </td>
          </tr>
        </tbody>
      </table>
      <!-- 应收应付报表 -->
      <div v-if="type===21" style="font-weight:bolder;font-size:16px;margin-bottom:15px;">
        <div style="text-align:right;margin:15px 0px;">
          应收总计：{{ data.yinshouSum }}<br>
          实收总计：{{ data.shishouSum }}<br>
          应付总计：{{ data.yinfuSum }}<br>
          实付总计：{{ data.shifuSum }}<br>
        </div>
        <div style="text-align:right">
          <span>
            应收未收：<span style="margin-left:6%"><Icon type="logo-yen" />{{ getSum(data.yinshouSum,data.shishouSum) }}</span>
          </span>
          <span style="margin-left:10%">
            应付未付：<span style="margin-left:6%"><Icon type="logo-yen" />{{ getSum(data.yinfuSum,data.shifuSum) }}</span>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import exportData from '@/common/exportData.js'
import JsBarcode from 'jsbarcode'
import calcu from '@/common/calculation.js'
import axiosUtils from '@/common/axiosUtils.js'
export default {
  name: 'PrintView',
  data() {
    return {
      isPrint: false,
      columns: [],
      list: [],
      type: parseInt(this.$route.query.type),
      data: {} // 特殊场合使用 比如应收应付报表
    }
  },
  beforeRouteEnter(to, from, next) {
    if (to.query.title) {
      document.title = to.query.title
    }
    next()
  },
  mounted() {
    this.search()
  },
  methods: {
    search() {
      const that = this;
      const type = that.type
      const query = that.$route.query;
      // 特殊处理
      this.addNewData(type, query)
      const param = exportData.dataParam[type]
      if (param) {
        that.$Spin.show({
          render: (h) => {
            return h('div', [
              h('Icon', {
                'class': 'demo-spin-icon-load',
                props: {
                  type: 'ios-loading',
                  size: 18
                }
              }),
              h('div', '数据正在打印中，请稍候......')
            ])
          }
        });
        const flag = param.flag;
        const isAll = query.isAllSelection ? parseInt(query.isAllSelection) : true;
        if (isAll) {
          (flag ? axiosUtils : that.commonUtil).ajaxPostUtil(param.url, param.searchData || { ascending: false }, (res) => {
          // 关闭数据加载
            that.$Spin.hide();
            that.responseDeal(param, res, flag, type);
          }, () => {
            that.$Message.error('打印失败');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        } else {
          axiosUtils.ajaxGetUtil(param.otherUrl + query.ids, {}, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            that.responseDeal(param, res, flag, type);
          }, () => {
            that.$Message.error('打印失败');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        }
      } else {
        that.$Message.error('打印失败');
      }
    },
    // response处理
    responseDeal(param, res, flag, type) {
      const that = this;
      if (flag ? res.ok : res.code === 1) {
        that.columns = param.columns
        const list = exportData.returnData(param.listFlag ? res.data : res.data[param.listStr], type)
        that.list = list
        // 档案信息
        if (param.barCode) {
          this.$nextTick(() => {
            for (const item of list) {
              JsBarcode('#code' + item.id, item.code, {
                format: 'CODE128', //  选择要使用的条形码类型
                text: item.code,
                displayValue: true, // 是否在条形码下方显示文字
                textPosition: 'bottom'// 设置文本的垂直位置
              })
            }
          })
        }
        if (type === 21) {
          that.data = res.data
        }
        that.$nextTick(() => {
          that.print()
        })
      } else {
        that.$Message.error(flag ? res.message : res.msg);
      }
    },
    print() {
      this.isPrint = false
      this.$nextTick(() => {
        window.print()
        this.isPrint = true
      })
    },
    // 特殊处理的数据 --- 审核情况
    addNewData(type, query) {
      if (type === 9) {
        const data = Object.assign({}, exportData.dataParam[8])
        data.searchData = { adminFlag: true, ascending: false }
        exportData.dataParam[9] = data
      } else if (type === 21) {
        const data = Object.assign({}, exportData.dataParam[16])
        exportData.dataParam[21] = data
      } else if (type === 18) {
        // 档案信息
        exportData.dataParam[18].searchData = {
          ids: query.ids
        }
      }
    },
    // 应收应付报表
    getSum(sum1, sum2) {
      return calcu.accSub(sum1 || 0, sum2 || 0) || ''
    }
  }
}
</script>
<style scoped>
  table,tr,td{text-align:center;}
  .ivu-table th {
    text-align: center;
  }
  body {
    height: 100%;
    background: #fff;
  }
</style>



